/* styles.css */

body {
  font-family: Arial, sans-serif;
  margin: 1rem;
  background-color: #f5f7fa;
  color: #333;
}

.container {
  max-width: 900px;
  margin: 1.5rem auto;
  padding: 1rem 2rem;
  background: white;
  border-radius: 8px;
  box-shadow: 0 4px 15px rgba(0,0,0,0.1);
}

h1 {
  text-align: center;
  margin-bottom: 1.5rem;
  color: #2c3e50;
}

.row {
  display: flex;
  gap: 0.75rem;
  margin-bottom: 1rem;
  flex-wrap: wrap;
}

.input {
  flex-grow: 1;
  padding: 0.5rem 0.75rem;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 4px;
}

button {
  background-color: #1e90ff;
  border: none;
  color: white;
  padding: 0 16px;
  font-size: 1rem;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.25s ease;
}

button:hover {
  background-color: #005bb5;
}

.filters {
  display: flex;
  gap: 0.75rem;
  margin-bottom: 1.5rem;
  flex-wrap: wrap;
}

.filters select,
.filters input {
  padding: 0.5rem 0.75rem;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  min-width: 120px;
  flex-grow: 1;
  max-width: 200px;
}

.products {
  display: grid;
  grid-template-columns: repeat(auto-fill,minmax(250px,1fr));
  gap: 1rem;
  padding: 0;
  list-style: none;
}

.products li {
  background: #fafafa;
  border: 1px solid #ddd;
  border-radius: 6px;
  padding: 1rem;
  box-shadow: 0 1px 3px rgb(0 0 0 / 0.1);
  transition: box-shadow 0.3s ease;
}

.products li:hover {
  box-shadow: 0 6px 15px rgb(0 0 0 / 0.15);
}

.products b {
  color: #2c3e50;
  font-size: 1.1rem;
  display: block;
  margin-bottom: 0.3rem;
}

.products span.price {
  color: #27ae60;
  font-weight: 600;
  margin-bottom: 0.4rem;
  display: block;
}

.products i {
  font-style: italic;
  color: #7f8c8d;
  margin-bottom: 0.5rem;
  display: block;
}

.products .rating {
  font-weight: 700;
  color: #e67e22;
}

@media (max-width: 480px) {
  .row {
    flex-direction: column;
  }

  .filters {
    flex-direction: column;
  }

  .filters select,
  .filters input,
  button {
    max-width: 100%;
    flex-grow: unset;
  }
}
